<template>
	<section>
		<div class="wlcp-banner"></div> 
		<div class="inner">
	       <div class="tran-title">VESSEL DETAILS </div>
	       <ul class="tran-line tra-cp"><li></li><li class="tran-line1"></li></ul>
	        <div class="ship-info clearfix">
	            <div class="c-tabdiv1 c-cpxx">
	                <table>                                                                                                  
	                    <tr>
	                    	<th>Vessel Name：<em>{{infos.name}}</em></th>
	                    	<th>Vessel Type：<em>{{infos.type}}</em></th>
	                    	<th>Vessel Age：<em>{{infos.age}}</em></th>
	                    	<th>Open:{{infos.open}}</th>
	                    </tr>
                    	<tr class="c-tabcos1">
                    		<td>Position：<em>{{infos.position}}</em></td>
                    		<td>ETA Fujairah：<em>{{infos.eta}}</em></td>
                    		<td>ETA Cabinda：<em>{{infos.etaCabinda}}</em></td>
                    		<!--<td><a href="javascript:;" v-if="isShow" @click="getBoot(infos.uuid)" class="c-charter">Chartering</a></td>-->
                    	</tr>
	                </table>
	              </div>
	        </div>
	        <div class="ship-info clearfix">
	            <div class="c-tabdiv1 c-cpxxt">
	                <table>                                                                                                  
	                    <!--<tr><th>IMO Nr船舶代码</th><th>Vessal Name船名</th><th>Last Name曾用名</th><th>FLAG船旗</th><th>Type船型</th><th>Builder建造商</th><th>CLASS船级社</th><th>BLT建造年份</th><th>Hull船体</th><th>LOA船长</th><th>BM船宽</th><th>DRAFT吃水</th><th>DWT载重吨</th><th>下载附件</th></tr>
	                    <tr class="c-tabcos1"><td>{{tableInfo.imo}}</td><td>{{tableInfo.name}}</td><td>{{tableInfo.useType}}</td><td>Hong Kong</td><td>{{tableInfo.type}}</td><td>Daewoo</td><td>ccs</td><td>{{tableInfo.completeDate}}</td><td>{{tableInfo.hull}}</td><td>{{tableInfo.length}}米</td><td>{{tableInfo.wide}}米</td><td>{{tableInfo.draft}}米</td><td>{{tableInfo.loadQuantity}}mt</td><td><a :href="'http://image8.1chemic.com//'+tableInfo.PATH" class="ship-xz" download="">下载附件</a></td></tr>-->
	                    <tr>
							<th>Vessel Name</th>
							<th>IMO</th>
							<th>Built</th>
							<th>Vessel Type</th>
							<th>Vessel Size</th>
							<th>Cubic</th>
							<th>SDWT</th>
							<th>LOA</th>
							<th>Beam</th>
							<th>Draft</th>
							<th>Hull Type</th>
							<!--<th>下载</th>-->
						</tr>
						<tr>
							<td>{{tableInfo.name}}</td>
							<td>{{tableInfo.imo}}</td>
							<td>{{tableInfo.completeDate}}</td>
							<td>{{tableInfo.useType}}</td>
							<td>{{infos.type}}</td>
							<td>{{tableInfo.capacity}}</td>
							<td>{{tableInfo.loadQuantity}}</td>
							<td>{{tableInfo.length}}</td>
							<td>{{tableInfo.wide}}</td>
							<td>{{tableInfo.draft}}</td>
							<td>{{tableInfo.hull}}</td>
							<!--<td>
								<a :href="'http://image8.1chemic.com//'+tableInfo.PATH" class="ship-xz" download="">下载附件</a>
							</td>-->
						</tr>
	                </table>
	              </div>
	              <span class="">Vessal data for reference only</span>
	        </div>
	        <ul class="wl-cp-com clearfix">
	          <li>
	            <div class="wl-cpcl"><i class="shipping-ico ico-cpcd"></i></div>
	            <div class="wl-cpcr">Ship Owner<span>{{infos.shipOwner}}</span></div>
	          </li>
	          <li>
	            <div class="wl-cpcl"><i class="shipping-ico ico-cpcj"></i></div>
	            <div class="wl-cpcr">Broker<span>{{infos.brokerName}}</span></div>
	          </li>
	        </ul>
	        <ul class="wl-cp-com clearfix">
	          <li>
	            <div class="wl-cpcl"><i class="shipping-ico ico-cpcf"></i></div>
	            <div class="wl-cpcr">Publisher<span>{{infos.createName}}</span></div>
	          </li>
	          <li>
	            <div class="wl-cpcl"><i class="shipping-ico ico-cpcl"></i></div>
	            <div class="wl-cpcr">Email<span>{{infos.email}}</span></div>
	          </li>
	          <li>
	            <div class="wl-cpcl"><i class="shipping-ico ico-cpcs"></i></div>
	            <div class="wl-cpcr">Publish Date<span>{{infos.createDate | timestampFormat('YYYY-MM-DD')}}</span></div>
	          </li>
	        </ul>
		</div>
		<div class="tool_ship">
			<span v-if="isShow"  @click="getBoot(infos.uuid)">Chartering</span>
			<span @click="goback">Back</span>
		</div>
	</section>
</template>

<script>
	import { bootsInfo,userLoad } from '../../api/ship';
	export default{
		data(){
			return{
				infos:{},
				tableInfo:{},
				str:'',
				isShow:false
			}
		},
		methods:{
			goback(){
				this.$router.go(-1)
			},
			getBoot(id){
				window.open(configURL.shipUrl+'/pallet/palletEdit.htm?palletType=1&shipPlateUuid=' + id,"_self");
			},
			infosFn:function(){
//				var str = this.$route.query.id;
				var para = {"uuid":this.str};
				bootsInfo(para).then(res => {
					if(res.datas.ship == null){
						this.infos = {}
						this.tableInfo = {}
					}else{
						this.tableInfo = res.datas.ship
						this.infos = res.datas
					}
					
				})
			},
			content(){
				if(this.$own.getHref('uuid')){	
					this.str=decodeURIComponent(this.$own.getHref('uuid'));
				}else{
					this.str=window.localStorage.getItem('uuidd');
				}
			},
			userdata(){
				userLoad().then((res)=>{
					if(res.status==0){
						var userData = res.datas;
						if(userData){
			    			var flag = false;
			    			if(userData.roles != null){
			    				for(var i=0; i<userData.roles.length; i++){
			    					var role = userData.roles[i];
					    			if( role == 'sales_trader' || role == 'buy_trader'|| role == 'charterer' || role == 'trade_executor'){
					    				flag = true
					    				this.isShow = true
					    			}
					    		}
			    			}
			    		}	
					}
				})	
			}
		},
		mounted(){
			this.infosFn()
			
		},
		created(){
			this.userdata()
			this.content();
		}
	}
</script>

<style>
	.tran-title{
		color: #222 !important;
	}
	.tool_ship{
		text-align: center;
		margin-bottom: 50px;
	}
	.tool_ship span{
		cursor: pointer;
		color: #fff;
	    font-size: 16px;  
	    padding: 10px;
	    background: #66a6fe;
	    text-align: center;
	    margin-left: 20px;
	}
</style>


